@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
:root {
    --a: #8ee4af;
    --b: #edf5e1;
    --c: #5cdb95;
    --d: #907163;
    --e: #379683;
  
    --dark-green: #61892f;
    --light-green: #86c232;
    --black: #222629;
    --dark-grey: #474b4f;
    --light-grey: #e6e3e3;
  
    --blackish: #202020;
    --dark-grey: #3f3f3f;
    --medium-grey: #b1aeae;
    --egg-yellow: #e0cf30;
    --white: #ffffff;

    --error: #fa6060;
  }
* {
  padding: 0px;
  margin: 0px;
  font-family: Nunito;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: var(--light-grey);
}

ul {
  list-style-type: none;
}

h1 {
  color: var(--dark-green);
  font-size: 2em;
  text-align: center;
}
h2 {
  font-size: 1.4em;
}

p {
  font-size: 1.1em;
  line-height: 1.5em;
  text-align: center;
  padding: 5px 10px;
}

footer {
  background-color: var(--black);
  color: var(--dark-green);
  border-top: 2px solid var(--dark-grey);
  padding: 10px 0;
}
footer p {
  font-size: 1em;
}

header {
    background-color: var(--black);
    padding-top: 20px;
  }
  
  header nav {
    max-width: 960px;
    position: relative;
    margin: 0 auto;
    padding: 0 10px;
  }
  
  nav h2 {
    text-align: center;
    color: var(--white);
  }
  
  div.styles_menuIcon__CYnwV {
    position: absolute;
    top: 0;
    right: 10px;
  }
  div.styles_menuIcon__CYnwV div {
    width: 30px;
    height: 5px;
    margin: 5px 0;
    background-color: var(--dark-green);
  }
  div.styles_menuIcon__CYnwV:hover {
    cursor: pointer;
  }
  
  .styles_mobMenu__3aL6T {
    transition: height 0.5s;
    height: 0;
    overflow: hidden;
    margin-top: 20px;
  }
  .styles_toggleMenu__qUdqm {
    height: 200px;
    padding-bottom: 20px;
  }
  nav ul li {
    color: var(--dark-green);
    padding: 5px 10px;
    transition: background-color 0.2s ease-in;
  }
  nav ul li:hover {
    background-color: var(--dark-grey);
    color: var(--light-green);
    border-radius: 4px;
    cursor: pointer;
  }
  
  nav form {
    display: flex;
    margin: 5px auto;
    max-width: 500px;
  }
  nav form input[type="text"] {
    flex: 2 1;
    border: 2px solid var(--dark-green);
    border-radius: 2px;
    padding: 10px 5px;
    margin-right: 10px;
    background-color: rgba(0, 0, 0, 0.2);
    color: var(--white);
    outline: none !important;
  }
  nav form button {
    flex: 1 1;
    background-color: rgba(0, 0, 0, 0.2);
    border: 2px solid var(--dark-green);
    color: var(--dark-green);
    font-size: 1em;
    font-weight: bold;
    border-radius: 50px;
    transition: background-color 0.2s;
  }
  nav form button:hover {
    background-color: var(--light-green);
    border: 2px solid var(--light-green);
    color: var(--white);
    cursor: pointer;
  }

  @media screen and (min-width: 900px) {
    .styles_menuIcon__CYnwV {
      display: none;
    }
    header {
      padding: 20px 0;
    }
    nav {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .styles_mobMenu__3aL6T {
      display: flex;
      align-items: center;
      height: auto;
      margin: 0;
    }
    ul {
      display: flex;
      
    }
    nav form{
      margin: 0 auto;
    }
    nav form input[type="text"] {
      margin: 0px;
      margin-left: 20px;
      border-radius: 20px 0 0 20px;
      padding: 5px;
      padding-left: 10px;
      width:200px;
      border-right: none;
    }
    nav form button {
      flex: 1 1;
      background-color: var(--dark-green);
      color: var(--white);
      border-radius: 0 20px 20px 0;
    }
  
    .styles_toggleMenu__qUdqm {
      padding-bottom: 0;
    }
  }
.styles_social__3nfr4 {
  width: 30px;
  position: fixed;
  right: 0;
  top: 45%;
  padding: 7px;
  padding-bottom: 2px;
  border-radius: 4px 0 0 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0);
  background-color: var(--white);
}
.styles_social__3nfr4 img {
  opacity: 0.2;
  transition: opacity 0.2s;
}
.styles_social__3nfr4 img:hover {
  opacity: 1;
  cursor: pointer;
}

.styles_chevron__2QzPU {
  position: fixed;
  width: 25px;
  height: 25px;
  bottom: 60px;
  right: -65px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  padding: 16px  20px 20px 16px;
  background-color: rgba(0,0,0, 0.2);
  border-radius: 50%;
  cursor: pointer;
  transition: right 1s ease;
}
.styles_chevron__2QzPU div {
  width: 100%;
  height: 100%;
  border-top: 10px solid var(--dark-green);
  border-left: 10px solid var(--dark-green);
  border-radius: 3px;
}

.styles_wrapper__2hDWy {
  max-width: 960px;
  margin: 0 auto;
}

/* ABOUT */
#styles_about__lYKcO {
  margin: 20px 0;
  text-align: center;
}

#styles_about__lYKcO .styles_profile__BqbUP img {
  border: 4px solid var(--dark-green);
  box-sizing: border-box;
  border-radius: 50%;
  width: 100%;
  max-width: 365px;
}
#styles_about__lYKcO .styles_profile__BqbUP p {
  color: var(--dark-grey);
}
.styles_skillSet__2W3iT {
  padding: 0 10px;
  display: flex;
  flex-wrap: wrap;
}
#styles_about__lYKcO h2 {
  margin-top: 10px;
  padding-left: 10px;
  color: var(--black);
}
.styles_skill__2UAY8 {
  flex-grow: 1;
  flex: 1 1 300px;
  margin: 10px 10px 20px 10px;
}
.styles_skill__2UAY8 ul {
  display: flex;
  flex: 1 1;
  margin: 5px 0;
}
.styles_skill__2UAY8 ul li {
  flex-grow: 1;
}
.styles_skill__2UAY8 ul li:nth-child(1) {
  text-transform: uppercase;
  font-weight: bold;
  color: var(--dark-grey);
}
.styles_skill__2UAY8 ul li:nth-child(2) {
  text-align: right;
}
.styles_fillPath__1PNWT {
  width: 100%;
  height: 10px;
  background-color: var(--medium-grey);
}

.styles_percentageFill__30OF5 {
  height: inherit;
  background-color: var(--dark-green);
}

/* PROJECTS */
#styles_projects__1bWuT {
  background: var(--black);
  padding: 25px 0;
}

#styles_projects__1bWuT p {
  color: var(--white);
}

.styles_projectsImg__2zg79 {
  padding: 20px 30px;
  /* width:512px; */
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(2, 1fr);
}

.styles_projectsImg__2zg79 img {
  max-width: 100%;
}

.styles_projectContainer__2H9I1 {
  position: relative;
}

.styles_projectTitle__2GPFK {
  position: absolute;
  box-sizing: border-box;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* Black see-through */
  color: var(--white);
  width: 100%;
  transition: 0.5s ease;
  font-size: 0.8em;
  padding: 7px 10px;
  text-align: center;
}

/* SERVICES */
#styles_services__3ZVon {
  padding: 25px 0;
}

#styles_services__3ZVon p {
  color: var(--dark-grey);
}
.styles_serviceType__1_n0h .styles_type__3o85C {
  text-align: center;
}
.styles_serviceType__1_n0h .styles_type__3o85C div {
  background-color: var(--dark-green);
  width: 12%;
  max-width: 50px;
  margin: 0 auto;
  padding: 23px;
  border-radius: 50%;
}
.styles_serviceType__1_n0h .styles_type__3o85C p {
  color: var(--dark-grey);
}

.styles_serviceType__1_n0h .styles_type__3o85C img {
  text-align: center;
  width: 100%;
}

/* CONTACT */
#styles_contact__2wX69 {
  padding: 25px 0;
  background-color: var(--black);
}

.styles_address__1PLVJ {
  margin: 10px 0;
  color: var(--white);
  text-align: center;
}

.styles_contactForm__3xd0R form {
  margin: 10px auto;
  width: 86%;
  padding: 26px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 3px;
}
.styles_contactForm__3xd0R form input,
textarea {
  display: flex;
  border: 2px solid var(--black);
  border-bottom: 2px solid var(--dark-green);
  padding: 10px 5px;
  border-radius: 2px;
  margin-top: 10px;
  background-color: var(--black);
  color: var(--white);
  outline: none !important;
  width: 100%;
  box-sizing: border-box;
}

textarea {
  resize: none;
  overflow-y: auto;
}
/* width */
textarea::-webkit-scrollbar {
  width: 10px;
}

/* Track */
textarea::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px var(--dark-grey);
  border-radius: 10px;
}

/* Handle */
textarea::-webkit-scrollbar-thumb {
  background: var(--dark-green);
  border-radius: 10px;
}

/* Handle on hover */
textarea::-webkit-scrollbar-thumb:hover {
  background: var(--light-green);
}

span.styles_err__2s6mr {
  margin: 3px;
  font-size: 0.8em;
  color: var(--error);
}

span.styles_success__VWZYq {
  color: var(--light-green);
  display: block;
  font-size: 1em;
  text-align: center;
  opacity: 1;
  transition: opacity 1s ease-out;
}

.styles_contactForm__3xd0R form button {
  min-width: 130px;
  outline: none !important;
  margin-top: 15px;
  padding: 7px 15px;
  background-color: var(--dark-green) !important;
  color: var(--white);
  border: 2px solid var(--white);
  border-radius: 4px;
  display: block;
}

.styles_btnDisabled__2Q18p {
  cursor: not-allowed;
  opacity: 0.7;
  background-color: var(--dark-grey);
}

/* --------------------MEDIA QUERIES-------------------- */

@media screen and (min-width: 480px) {
  /* ABOUT */
  #styles_about__lYKcO .styles_profile__BqbUP {
    margin-top: 15px;
  }
  #styles_about__lYKcO .styles_profile__BqbUP img {
    width: 50%;
    max-width: 300px;
    float: left;
    margin: 0 20px;
  }
  #styles_about__lYKcO .styles_profile__BqbUP p {
    text-align: justify;
  }
  #styles_about__lYKcO .styles_profile__BqbUP:after {
    content: "";
    display: block;
    clear: both;
  }

  /* PROJECT */
  .styles_projectsImg__2zg79 {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 720px) {
  /* ABOUT */
  #styles_about__lYKcO .styles_profile__BqbUP img {
    max-width: 300px;
  }

  /* SERVICES */
  #styles_services__3ZVon {
    padding-bottom: 0;
  }

  .styles_serviceType__1_n0h {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-auto-rows: minmax(10%, auto);
    margin-top: 20px;
  }

  .styles_webDesign__1YUS- {
    grid-column: 2/6;
  }

  .styles_respDesign__YmD-O {
    grid-column: 8/12;
  }

  .styles_webDev__362pN {
    grid-column: 5/9;
    grid-row: 2/3;
    position: relative;
    bottom: 50px;
  }

  .styles_serviceType__1_n0h .styles_type__3o85C div {
    width: 30%;
    max-width: 55px;
    padding: 23px;
  }

  /* CONTACT */
  .styles_contactInfo__1nkwq {
    display: flex;
    flex: 1 1;
    align-items: center;
  }
  .styles_address__1PLVJ,
  .styles_contactForm__3xd0R {
    flex-grow: 1;
  }
}

@media screen and (min-width: 900px) {
  /* PROJECTS */
  .styles_projectsImg__2zg79 {
    grid-template-columns: repeat(4, 1fr);
  }

  .styles_projectsImg__2zg79 img {
    -webkit-filter: grayscale(0);
    filter: grayscale(0);
    transition: -webkit-filter 0.3s;
    transition: filter 0.3s;
    transition: filter 0.3s, -webkit-filter 0.3s;
  }

  .styles_projectContainer__2H9I1:hover .styles_projectTitle__2GPFK {
    opacity: 1;
  }

  .styles_projectContainer__2H9I1:hover img {
    content: "";
    opacity: 0.5;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
  }

  .styles_projectTitle__2GPFK {
    opacity: 0;
    padding: 20px 10px;
  }

  /* CONTACT */
  .styles_contactForm__3xd0R {
    flex-grow: 2;
  }
}

